html {
  background-color: #282c34;
  max-width: 95%;
}

/*
    Toggle Button
*/

.expandButton{ 
  background-color: #282c34;
  font-size: 50px;
  color: #6F7C80;
  border: 0px;
  height: 100px;
  width: 100px;
}

/*
    Main Everything Container
*/

.grid-container {
  display: grid;
  margin: 75px 0;
  height: 400px;
  grid-template-rows: 0% 100%;
  grid-template-columns: 100% 0%;
  transition-duration: 1s;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  /* border: 2px solid white; */
}
.open-grid {
  grid-template-rows: 60% 40%;
  grid-template-columns: 35% 65%;
  margin: 20px 0;
  height: 600px;
}
/*
    Introduction Large size
*/

.hello {
  display: block;
  text-align: center;
  font-size: 50px;
  grid-column: 1 / span 1;
  grid-row: 2 / span 1;
  transition-duration: 1s;
  /* border: 2px solid white; */
  background: linear-gradient(.25turn, #F2F3F4, #6F7C80);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

/*
    Introduction Expanded
*/

.helloExpand {
  font-size: 25px;
  transition-duration: 1s;
}

/*
    Image Container and img
*/
.imageCont {
  height: 0px;
  grid-column: 1 / span 1;
  grid-row: 1 / span 1;
  /* border: 2px solid white; */
}
.profile-pic {
  display: block;
  margin: auto;
  height: 0px;
  transition-duration: 1s;
  border-radius: 50%;
}
/*
    Image Container and img Expanded
*/

.pic-expand {
  display: block;
  height: 300px;
  margin: auto;
}
/*
    Main Content Holder(description, projects, contact)
*/
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 75px;
  color: #6F7C80;
  padding: 25px;
  grid-column: 2 / span 1;
  grid-row: 1 / span 2;
  font-size: 0px;
  transition-duration: 1s;
}
.button-holder {
  display: none;
  width: 50px;
  gap: 50px;
  transition-duration: .5s;
  transition-delay: 1s;
}
.nav-button {
  font-size: 0px;
  height: 0px;
  background-color: #282c34;
  border: 1px solid #6F7C80;
  color: #6F7C80;
  box-shadow: 0px 1px;
  border-radius: 5px;;
  transition-duration: 1s;
}

/*
    Main Content Holder(description, projects, contact)
*/

.content-expanded{ 
  font-size: 50px;
  transition-duration: 1s;
}
.nav-button-expanded {
  height: 50px;
  width: 150px;
  font-size: 25px;
  transition-delay: .4s;
  transition-duration: 1s;
}
.text {
  position: relative;
  height: 480px;
}
.description {
  transition-duration: 1s;
  font-size: 30px;
  line-height: 50px;
  max-width: 1150px;
  min-width: 1000px;
  height: 480px;
}
.contact {
  transition-duration: 1s;
  font-size: 30px;
  max-width: 1150px;
  min-width: 1000px;
  line-height: 50px;
  height: 480px;
}
.slider-grid-container {
  transition-duration: 1s;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 1000px;
  height: 480px;
  overscroll-behavior-inline: contain;
}
.slider-grid {
  display: grid;
  grid-auto-flow: column;
}
.project-slide {
  z-index: 1;
  position: relative;
  width: 1000px;
  height: 450px;
  display: grid;
  gap: 1px;
  grid-template-columns: repeat(9, 11%);
  grid-template-rows: repeat(9, 11%);
  border-radius: 5px;
}
.photo-portrait {
  grid-column: 1 / 3 span;
  grid-row: 1 / 9 span;
  height: 100%;
  width: 100%;
  max-height: 450px;
  border-radius: 5px 0 0 5px;
}
.photo-landscape {
  grid-column: 4 / 6 span;
  grid-row: 1 / 4 span;
  height: 100%;
  width: 100%;
  border-radius: 0 5px 0 0;
}
.project-description {
  grid-column: 4 / 6 span;
  grid-row: 5 / 5 span;
  height: 100%;
  font-size: 20px;
  color: black;
  background-color: #6F7C80;
  height: 100%;
  max-height: 249px;
  border-radius: 0 0 5px 0;
}
.project-description > h4 {
  text-align: center;
  margin: 5px 0;
  border-bottom: 1px solid black;
  width: 80%;
  padding: 5px;
  margin: auto;
}
.project-description > div {
  padding: 10px;
}
.project-button-holder {
  display: flex;
  gap: 20px;
  justify-content: center;
}
.project-button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  height: 30px;
  width: 100px;
  background-color: #a6a6a6;
  border: 1px solid #282c34;
  color: black;
  box-shadow: 0px 1px;
  border-radius: 5px;
  transition-duration: 1s;
}
.project-button > a {
  text-decoration: none;
}

.project-button > a:visited {
  color: black
}
.navButton {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  opacity: 50%;
  height: 450px;
  color: black;
}
.navButton:hover {
  transition-duration: .5s;
  background-color: #6F7C80;
  opacity: 50%;
}
.prev {
  left: 0;
  z-index: 5;
  border-radius: 5px 0 0 5px;
}
.next {
  right: 0;
  z-index: 5;
  border-radius: 0 5px 5px 0;
}
.hidden {
  transition-duration: 1s;
  font-size: 0px!important;
  line-height: 0px;
  height: 0px;
  width: 0px;
}
.flex {
  display: flex;
  width: 750px;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
 